.Home_headerFrame {
  display: flex;
  flex: 1; }

.Home_bodyFrame {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 3; }

.Home_footerFrame {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding-bottom: 20px;
  box-sizing: border-box;
  flex: 1; }

.Home_noticeRow {
  display: flex;
  width: 400px;
  justify-content: space-between; }

.Home_footerPrint {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 13px;
  color: rgba(0, 0, 0, 0.4); }
