.textInput {
  height: 50px;
  width: 200px;
  outline: none;
  padding: 0px;
  padding-left: 10px;
  padding-right: 10px;
  margin-top: 20px;
  font-size: x-large;
  color: rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  user-select: none; }
