.billBoardFrame {
  width: 400px; }

.billBoard {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  font-style: italic;
  margin-top: 20px;
  width: 100%;
  height: 100px;
  box-sizing: border-box;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  border: 1px solid rgba(0, 0, 0, 0.1);
  user-select: none; }

.underline {
  transition: 1s;
  width: 0%;
  height: 2px; }
