.Result_headerFrame {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex: 1; }

.Result_bodyFrame {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 3; }

.Result_footerFrame {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding-bottom: 20px;
  box-sizing: border-box;
  flex: 1; }

.Result_restartButton {
  width: 300px;
  height: 50px;
  outline: none;
  padding: 0px;
  margin: 0px;
  margin-top: 30px;
  font-size: 20px;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  cursor: pointer; }
  .Result_restartButton:active {
    background: rgba(0, 0, 0, 0.05); }

.Result_footerPrint {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 13px;
  color: rgba(0, 0, 0, 0.4); }
