.decisionButton {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 30px;
  width: 200px;
  background: white;
  border: 0px;
  outline: none;
  margin-top: 20px;
  padding: 0px;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  user-select: none; }
  .decisionButton:active {
    background: rgba(0, 0, 0, 0.05); }

.disabled {
  pointer-events: none;
  background: rgba(0, 0, 0, 0.05);
  cursor: not-allowed; }

.image {
  height: 25px;
  width: 25px;
  display: none; }

.text {
  margin: 0px; }

.visible {
  display: block; }
